


.containers{
  position: absolute;
  width: 50%;
  margin-top: -50px;
  .popupImg{
    width: 500px;
    justify-content: center;
    align-items: center;
    left: 30%;
    position: relative;
    img{
      z-index: 99999;
      width: 500px;
      height: 500px;
    }
    button{
      color: white;
      font-size: 15px;
    }
  }

  .img-grid{
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    img{
      width: 50px;
      height: 50px;
      cursor: pointer;
      border: 2px solid transparent;
      &:hover{
        opacity: 0.3;
        border: 2px solid hsl(26, 100%, 55%);
      }
    }
  }
  .btn-close-popupImg{
    display: flex;
    width: 100%;
    background: transparent;
    border: none;
    margin-bottom: 10px;
    justify-content: flex-end;
    &:hover{
      color: hsl(26, 100%, 55%);
    }
  }

}


