.popup{
  position: absolute;
  z-index: 99;
  background-color: hsl(0, 0%, 100%);
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  border-radius: 8px;
  width: 22%;
  margin-top: 250px;
  right: 50px;
  padding-bottom: 20px;
  .popup-inner{
    position: relative;
    width: 100%;
    margin-top: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}


.popup-container{
  .div-top{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid;
    h3{
      margin-left: 15px;
      font-size: 16px;
    }
    .close-btn{
      background: transparent;
      border: none;
      cursor: pointer;
      font-size: 20px;
    }
  }
}
