
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Kumbh Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

button, img{
  cursor: pointer;
}

html, body{
  height: 100%;
}

code {
  font-family: 'Kumbh Sans', sans-serif;
}
