
img{
  border-radius: 12px;
}



.container{
  padding: 10% 20%;
  display: flex;
  gap: 150px;

}
.active{
  background: #252525;
  opacity: 0.3;
}

.img1{
  width: 300px;
  height: 300px;
}


.left{
  width: 39%;
  .img-grid{
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    img{
      width: 50px;
      height: 50px;
      cursor: pointer;
      border: 2px solid transparent;
      &:hover{
        opacity: 0.3;
        border: 2px solid hsl(26, 100%, 55%);
      }
    }
  }
}



.right{
  padding-top: 50px;
  h1{
    font-size: 30px;
  }
  p{
    font-size: 12px;
    color: hsl(240, 1%, 72%);
  }
  small{
    color:  hsl(26, 100%, 55%);
    font-weight: bolder;
  }
  .reduc{
    margin-left: 15px;
    font-size: 15px;
    align-items: center;
    color: hsl(26, 100%, 55%);
    background: hsl(25, 100%, 94%);
    border-radius: 6px;
    padding: 5px;
  }
  .oldPrice{
    text-decoration: line-through;
    color: hsl(240, 1%, 72%);
    font-size: 12px;
  }

  .right-btn{
    display: flex;
    gap: 15px;
    .number{
      display: flex;
      span{
        background: hsl(180, 6%, 93%);
        padding: 13px 20px;
      }
      button{
        cursor: pointer;
        background: hsl(180, 6%, 93%);
        border: none;
        color: hsl(26, 100%, 55%);
        font-size: 20px;
        &:nth-child(1){
          border-top-left-radius: 6px;
          border-bottom-left-radius: 6px;
          padding-left:  16px;
        }
        &:nth-child(3){
          border-top-right-radius: 6px;
          border-bottom-right-radius: 6px;
          padding-right: 16px;
        }
      }
    }
  }


  .addCart{
    border: none;
    background: hsl(26, 100%, 55%);
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    box-shadow: 10px 10px 40px 20px  hsl(25, 100%, 94%);
    transition: all 0.2s;
    &:hover{
      background: hsl(27, 95%, 78%);
    }
    .cart-icon{
      color: white;
    }
    button{
      background: transparent;
      border: none;
      color: white;
    }
  }

}






























































