.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10%;
  .links{
    margin-right: 450px;
    a{
      text-decoration: none;
      color: hsl(0, 0%, 0%);
      opacity: 70%;
      margin-right: 25px;
      &:hover{
        border-bottom: 5px solid hsl(26, 100%, 55%);
        padding-bottom: 42px;
      }
    }
  }

  .header-buttons{
    cursor: pointer;
    display: flex;
      .avatar{
        width: 50px;
        height: 50px;
        margin-left: 40px;
        border: 2px solid transparent;
        transition: all 0.4s;
        &:hover{
         border: 2px solid hsl(26, 100%, 55%);
         border-radius: 50%;
        }
      }
    .caddie{
      display: flex;
      align-items: center;

    }
  }
}


.border-bottom:after{
  content: ""; /* This is necessary for the pseudo element to work. */
  display: block; /* This will put the pseudo element on its own line. */
  margin: 0 auto; /* This will center the border. */
  width: 80%; /* Change this to whatever width you want. */
  padding-top: 20px; /* This creates some space between the element and the border. */
  border-bottom: 1px solid hsla(0, 0%, 0%, 0.44);
}


.purchase{
  height: 100%;
  button {
    cursor: pointer;
    background: hsl(26, 100%, 55%);
    margin-top: 20px;
    width: 100%;
    border-radius: 6px;
    padding: 10px 0;
    border: none;
    color: white;
    font-size: 15px;
  }
}


.purchase-content{
  color: hsl(240, 1%, 72%);
  font-weight: bold;
  display: flex;
  img{
    width: 50px;
    height: 50px;
  }
  p{
    margin: 0 0 0 10px;
    font-size: 13px;
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    .result{
      font-size: 14px;
      margin-left: 10px;
      color: black;
    }
  }
}


.notif{
  position: absolute;
  font-size: 8px;
  margin-left: 10px;
  margin-bottom: 20px;
  background: hsl(26, 100%, 55%);
  color: white;
  border-radius: 50px;
  padding: 2px 7px;
}


.bin{
  align-items: center;
  display: flex;
  margin-left: 10px;
  color: hsl(26, 100%, 55%);
  &:hover{
    color: red;
  }
}
